* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #434750;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.imgContainer {
  flex-direction: column;
  flex: 1 0 auto;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: relative;
}

.imgContainer-title {
  color: #ffffffd0;
  text-align: center;
  margin: 20px;
}

.imgContainer-subTitle {
  color: #16b1f8d3;
  text-align: center;
  margin: 10px;
  font-size: 16px;
  font-weight: 600;
}

.imgContainer-content {
  flex-direction: row;
  width: 100%;
  height: 100%;
  display: flex;
}

.imgContainer-content-left {
  text-align: center;
  background: #212f4ebe;
  border: 1px solid #989daaa2;
  border-radius: 10px;
  flex-shrink: 0;
  width: 20%;
  margin: 20px 0 20px 20px;
  position: relative;
}

.imgContainer-content-left-foldeNode {
  color: #fff;
  cursor: pointer;
  height: 50px;
  font-size: 14px;
  line-height: 50px;
  transition: all .5s;
}

.imgContainer-content-left-foldeNodePick {
  color: #fff;
  cursor: pointer;
  background: #7a7a7a44;
  height: 50px;
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 50px;
  transition: all .5s;
}

.imgContainer-content-left-foldeNode:hover {
  background: #7a7a7a44;
  margin-bottom: 10px;
  font-size: 16px;
}

.imgContainer-content-mid {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  row-gap: 10px;
  width: 100%;
  padding: 20px;
  display: flex;
}

.imgContainer-content-mid-imgShow {
  background: #212f4ebe;
  border: 1px solid #989daaa2;
  border-radius: 10px;
  flex-flow: wrap;
  flex-grow: 1;
  flex-shrink: 0;
  align-content: flex-start;
  width: 100%;
  padding: 10px;
  display: flex;
  overflow: hidden;
}

.imgContainer-content-mid-imgShow-item {
  cursor: pointer;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  min-width: 140px;
  min-height: 140px;
  transition: all .5s;
  display: flex;
}

.imgContainer-content-mid-imgShow-item:hover {
  box-shadow: 0 0 3px 3px #28acf8c7;
}

.imgContainer-content-mid-imgShow-item:hover .imgContainer-content-mid-imgShow-itemImg {
  min-width: 100px;
  min-height: 100px;
}

.imgContainer-content-mid-imgShow-item:hover .imgContainer-content-mid-imgShow-itemTitle {
  color: #ffffffce;
  background: #2ba8f0ce;
}

.imgContainer-content-mid-imgShow-itemTitle {
  color: #ffffff71;
  background: #5d5d6194;
  border-radius: 5px;
  margin-top: 5px;
  padding: 0 15px;
  font-size: 14px;
  transition: all .5s;
}

.imgContainer-content-mid-imgShow-itemImg {
  min-width: 80px;
  min-height: 80px;
  transition: all .5s;
}

